<template>
  <div class="modifyInfo">
    <van-nav-bar
      title="修改信息"
      left-text="返回"
      left-arrow
      fixed
      @click-left="$toPage(-1)"
    />
    <van-tabs v-model="storeDetail.storetype" @click="onClick" animated>
      <van-tab title="医疗机构" name="0">
        <div
          v-if="item.isShow"
          class="content"
          v-for="(item, index) in uploadList"
          :key="index"
        >
          <p class="name">{{ item.name }}</p>
          <div class="left" @click="clickRead(index)">
            <!-- <van-uploader :after-read="beforeRead" :disabled="disabled">
              <img v-lazy="item.url" />
            </van-uploader> -->
            <!-- <div
              v-show="disabled"
              class="bdImg showDiv"
              @click="viewImg(index)"
            ></div> -->
            <img
              v-show="!item.url"
              src="../../assets/error.png"
              class="bdImg"
              @click="reg_img_select(index)"
            />
            <img v-show="item.url" :src="item.url" @click="imgChange(index)" />
            <van-icon
              v-show="item.url"
              name="delete"
              @click="deleteImg(index)"
            />
          </div>
          <div class="right">
            <van-field
              v-model="item.certificateno"
              :disabled="disabled"
              placeholder="证件号"
              label="证件号"
              label-width="50px"
              input-align="center"
            />
            <van-field
              :class="item.must ? 'disabled' : 'disable'"
              v-model="item.pname"
              :disabled="item.must"
              :placeholder="placeholder(item.atype)"
              label="委托人"
              label-width="50px"
              input-align="center"
            />
            <div>
              <van-field
                v-model="item.validitys"
                :disabled="disabled"
                placeholder="起始有效期"
                class="input"
                input-align="center"
                clickable
                @click="selectTime(1, index)"
              />~
              <van-field
                v-model="item.validitye"
                :disabled="disabled"
                placeholder="结束有效期"
                class="input"
                input-align="center"
                clickable
                @click="selectTime(2, index)"
              />
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab title="药品经营企业" name="1">
        <div
          v-if="item.isShow"
          class="content"
          v-for="(item, index) in uploadList"
          :key="index"
        >
          <p class="name">{{ item.name }}</p>
          <div class="left">
            <!-- <van-uploader :after-read="beforeRead" :disabled="disabled">
              <img v-lazy="item.url" />
            </van-uploader> -->
            <!-- <div
              v-show="disabled"
              class="bdImg showDiv"
              @click="viewImg(index)"
            ></div> -->
            <img
              v-show="!item.url"
              src="../../assets/error.png"
              class="bdImg"
              @click="reg_img_select(index)"
            />
            <img v-show="item.url" :src="item.url" @click="imgChange(index)" />
            <van-icon
              v-show="item.url"
              name="delete"
              @click="deleteImg(index)"
            />
          </div>
          <div class="right">
            <van-field
              v-model="item.certificateno"
              :disabled="disabled"
              placeholder="证件号"
              label="证件号"
              label-width="50px"
              input-align="center"
            />
            <van-field
              v-model="item.pname"
              :disabled="item.must"
              :placeholder="placeholder(item.atype)"
              label="委托人"
              label-width="50px"
              input-align="center"
            />
            <div>
              <van-field
                v-model="item.validitys"
                :disabled="disabled"
                placeholder="起始有效期"
                class="input"
                input-align="center"
                clickable
                @click="selectTime(1, index)"
              />~
              <van-field
                v-model="item.validitye"
                :disabled="disabled"
                placeholder="结束有效期"
                class="input"
                input-align="center"
                clickable
                @click="selectTime(2, index)"
              />
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
    <van-popup class="popup" v-model="imgShow">
      <p @click="viewImg(uploadIndex)">
        {{ $t("messages.regist.lookImg") }}
      </p>
      <p @click="reg_img_select(uploadIndex)">
        {{ $t("messages.regist.changeImg") }}
      </p>
    </van-popup>
    <van-image-preview v-model="mask" lazy-load :images="showImg" />
    <van-popup v-model="show" position="bottom" :overlay="false">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        :visible-item-count="count"
        @cancel="cancel"
        @confirm="confirm"
      />
    </van-popup>
    <van-field
      v-model="storeDetail.invoiceVO.bankers"
      :disabled="disabled"
      placeholder="开户银行"
      label="开户银行"
      maxlength="20"
      @blur="blurHeight"
      @focus="focusHeight"
    />
    <van-field
      v-model="storeDetail.invoiceVO.account"
      type="tel"
      :disabled="disabled"
      placeholder="银行账号"
      label="银行账号"
      maxlength="30"
      @blur="blurHeight"
      @focus="focusHeight"
    />
    <van-field
      v-model="storeDetail.invoiceVO.tel"
      type="tel"
      :disabled="disabled"
      placeholder="注册电话"
      label="注册电话"
      maxlength="11"
      @blur="blurHeight"
      @focus="focusHeight"
    />
    <van-checkbox
      v-model="isControlSelling"
      :disabled="disabled"
      checked-color="#F91E1E"
      >是否签控销协议</van-checkbox
    >
    <van-checkbox
      v-model="isStoreSelling"
      :disabled="disabled"
      checked-color="#F91E1E"
      >是否签强控销协议</van-checkbox
    >
    <van-button
      type="danger"
      block
      @click="commit"
      :disabled="disabled"
      ref="button"
      >保存</van-button
    >
    <div v-show="overlay" class="overlay">
      <van-loading type="spinner" color="#fff" size="65px" />
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      overlay: false,
      mask: false,
      showImg: [],
      imgShow: false,
      text: "修改",
      disabled: false,
      show: false,
      currentDate: new Date(),
      minDate: 0,
      count: 3,
      current: new Date(),
      maxDate: new Date(2219, 0, 1),
      minDate: new Date(1919, 0, 1),
      nowDate: "",
      value: "",
      tag: "",
      index: "",
      isControlSelling: false,
      isStoreSelling: false,
      uploadList: [
        {
          aptid: 0,
          compid: 0,
          atype: 10,
          fileList: [],
          certificateno: "",
          url: "",
          name: "营业执照(必填)",
          validitys: "",
          validitye: "",
          message: "请上传营业执照",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: true,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 11,
          fileList: [],
          certificateno: "",
          url: "",
          name: "药店经营许可证(必填)",
          validitys: "",
          validitye: "",
          message: "请上传药店经营许可证",
          cstatus: "0",
          pname: "", //姓名
          isShow: false,
          required: true,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 12,
          fileList: [],
          certificateno: "",
          url: "",
          name: "GSP认证(必填)",
          validitys: "",
          validitye: "",
          message: "请上传GSP认证",
          cstatus: "0",
          pname: "", //姓名
          isShow: false,
          required: true,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 15,
          fileList: [],
          certificateno: "",
          url: "",
          name: "机构执业许可证(必填)",
          validitys: "",
          validitye: "",
          message: "请上传机构执业许可证",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: true,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 13,
          fileList: [],
          certificateno: "",
          url: "",
          name: "采购提货人委托书(必填)",
          validitys: "",
          validitye: "",
          message: "请上传采购提货人委托书",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: true,
          must: false
        },
        {
          aptid: 0,
          compid: 0,
          atype: 14,
          fileList: [],
          certificateno: "",
          url: "",
          name: "采购提货人身份证(必填)",
          validitys: "",
          validitye: "",
          message: "请上传采购提货人身份证",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: true,
          must: false
        },
        {
          aptid: 0,
          compid: 0,
          atype: 16,
          fileList: [],
          url: "",
          name: "诊疗科目副本(必填)",
          certificateno: "",
          validitys: "",
          validitye: "",
          message: "请上传诊疗科目副本",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: true,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 21,
          fileList: [],
          certificateno: "",
          url: "",
          name: "食品经营许可(选填)",
          validitys: "",
          validitye: "",
          message: "请上传食品经营许可",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: false,
          must: true
        },
        {
          aptid: 0,
          compid: 0,
          atype: 22,
          fileList: [],
          certificateno: "",
          url: "",
          name: "二类医疗器械许可(选填)",
          validitys: "",
          validitye: "",
          message: "请上传二类医疗器械许可",
          cstatus: "0",
          pname: "", //姓名
          isShow: true,
          required: false,
          must: true
        }
        //  {
        //   aptid: 0,
        //   compid: 0,
        //   atype: 23,
        //   fileList: [],
        //   certificateno: "",
        //   url: "",
        //   name: "三类医疗器械许可",
        //   validitys: "",
        //   validitye: "",
        //   message: "请上传三类医疗器械许可",
        //   cstatus: "0",
        //   pname: "", //姓名
        //   isShow: true,
        //   required: false
        // }
      ],
      bankInfo: {},
      storeDetail: {
        cid: 0, //企业码
        cname: "", //企业名称
        caddrcode: "0", //地区码
        caddr: "", //详细地址
        inviter: "0", //采集人用户uid
        storetype: 0, //门店类型
        uphone: "0", //电话号码
        cstatus: "0",
        control: 0,
        invoiceVO: {
          oid: "0",
          cid: "0",
          taxpayer: "",
          bankers: "",
          account: "",
          tel: "",
          cstatus: "0",
          email: ""
        }, //发票信息
        aptitudeVOS: [
          {
            aptid: "0", //资质id唯一码
            compid: 0, //企业id
            atype: "0", //资质类型 10-营业执照 11-药店经营许可证 12-gsp认证 13-采购/提货委托书 14-采购/提货人员复印件 15-医疗机构执业许可证(医疗单位)
            certificateno: "0", //证件编号
            validitys: "", //有效期始
            validitye: "", //有效期止
            cstatus: "0",
            pname: "" //姓名
          }
        ],
        busScopeVOS: [
          {
            bscid: "0", //
            compid: 0,
            busscope: "0", //经营范围码
            codename: "0", //经营范围名称
            cstatus: "0"
          }
        ], //企业经营范围
        caddrname: "", //地区名
        busScopeStr: "" //经营范围字符串
      },
      compid: 0,
      imgList: []
    };
  },
  mounted() {
    this.compid = this.$route.query.compid;
    this.getCompInfo();
    this.fileServerInfo();
  },
  methods: {
    onClickRight() {
      if (this.disabled) {
        Dialog.confirm({
          title: "温馨提示",
          message: "修改药店信息后需要重新审核~"
        }).then(() => {
          this.text = "取消";
          this.disabled = false;
        });
      } else {
        this.text = "修改";
        this.disabled = true;
      }
    },
    onClick(i) {
      if (i == 1) {
        this.uploadList[1].isShow = true;
        this.uploadList[2].isShow = true;
        this.uploadList[3].isShow = false;
        this.uploadList[6].isShow = false;
      } else {
        this.uploadList[1].isShow = false;
        this.uploadList[2].isShow = false;
        this.uploadList[3].isShow = true;
        this.uploadList[6].isShow = true;
      }
      this.storeDetail.storetype = i;
    }, //tab
    beforeRead(file) {
      //文件上传
      this.uploadList[this.selectImg].url = file.content;
    },
    clickRead(i) {
      this.selectImg = i;
    },
    selectTime(a, index) {
      this.show = true;
      this.tag = a;
      this.index = index;
    }, //选择时间
    cancel() {
      this.show = false;
    }, //  时间取消
    confirm() {
      this.show = false;
      var year = this.currentDate.getFullYear();
      var month = this.currentDate.getMonth() + 1;
      var date = this.currentDate.getDate();
      month = month < 10 ? "0" + month : month;
      date = date < 10 ? "0" + date : date;
      this.nowDate = year + "-" + month + "-" + date;
      if (this.tag == 1) {
        this.uploadList[this.index].validitys = this.nowDate;
      } else if (this.tag == 2) {
        this.uploadList[this.index].validitye = this.nowDate;
      }
    }, // 确认时间
    commit() {
      // this.disabled = true
      // this.text = "修改";
      this.overlay = true;
      this.iQualification(this.uploadList) && this.updateStoreInfo();
    },
    getCompInfo() {
      JNB.query({
        serverName: "userServer",
        cls: "BackGroundProxyMoudule",
        method: "getCompInfo",
        params: {
          compid: this.compid
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.storeDetail = result.data;
            if (result.data.control == 1) {
              this.isControlSelling = true;
            }
            if ((result.data.control & 2) > 0) {
              this.isStoreSelling = true;
              this.isControlSelling = true;
            }
            if (this.storeDetail.storetype !== 0) {
              this.storeDetail.storetype = 1;
            }
            this.onClick(result.data.storetype);
            result.data.aptitudeVOS.forEach(item => {
              this.uploadList.forEach(arr => {
                if (arr.atype == item.atype) {
                  arr.aptid = item.aptid;
                  arr.compid = item.compid;
                  arr.atype = item.atype;
                  arr.certificateno = item.certificateno;
                  arr.validitys = item.validitys;
                  arr.validitye = item.validitye;
                  arr.cstatus = item.cstatus;
                  arr.pname = item.pname;
                }
              });
            });
          } else {
          }
        }
      });
    },
    fileServerInfo() {
      JNB.query({
        serverName: "globalServer",
        cls: "FileInfoModule",
        method: "fileServerInfo",
        params: {
          compid: this.compid
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            var path = result.data.ergodicUrl;
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = () => {
              if (xhr.readyState === 4 && xhr.status === 200) {
                var data = xhr.responseText;
                data = JSON.parse(data).data.sort();
                this.initImgList(data, result);
              }
            };
            xhr.open("POST", path, true);
            xhr.setRequestHeader("specify-path", result.data.companyFilePath);
            xhr.setRequestHeader("ergodic-sub", "false");
            xhr.send(null);
          } else {
          }
        }
      });
    },
    initImgList(data, result) {
      for (var i in data) {
        this.uploadList.forEach(item => {
          let index = data[i].replace(/[^0-9]/gi, "");
          if (index == item.atype) {
            item.fileList = [
              {
                uid: item.atype,
                name: data[i],
                status: "done"
              }
            ];
            item.url =
              result.data.downPrev +
              result.data.companyFilePath +
              data[i] +
              "?" +
              new Date().getSeconds();
          }
        });
      }
    },
    updateStoreInfo() {
      this.storeDetail.aptitudeVOS = [];
      this.uploadList.forEach(item => {
        let flag = false;
        if (this.storeDetail.storetype) {
          if (item.atype !== 15 && item.atype !== 16) {
            flag = true;
          }
        } else {
          if (item.atype !== 11 && item.atype !== 12) {
            flag = true;
          }
        }
        if (
          flag &&
          ((item.certificateno !== "" && item.validitys !== "") ||
            item.validitye !== "")
        ) {
          this.storeDetail.aptitudeVOS.push({
            aptid: item.aptid,
            compid: item.compid,
            atype: item.atype,
            certificateno: item.certificateno,
            validitys: item.validitys,
            validitye: item.validitye,
            cstatus: item.cstatus,
            pname: item.pname
          });
        }
      });

      this.storeDetail.control = this.isStoreSelling
        ? 3
        : this.isControlSelling
        ? 1
        : 0;
      JNB.query({
        serverName: "userServer",
        cls: "BackGroundProxyMoudule",
        method: "storePerfectingInfo",
        params: this.storeDetail,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.resetImg();
          } else {
            this.$toast.fail(result.data);
            this.overlay = false;
          }
        }
      });
    },
    resetImg() {
      JNB.query({
        serverName: "globalServer",
        cls: "FileInfoModule",
        method: "fileServerInfo",
        params: {
          compid: this.compid
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            const list = [];
            const o = result.data;
            this.uploadList.forEach(data => {
              if (data.url.slice(0, 5) == "image") {
                list.push({
                  remotePath: o.companyFilePath,
                  fileName: data.atype + ".jpg",
                  uri: data.url
                });
              }
            });
            if (list.length > 0) {
              JNB._requestNative(
                "fileUpload",
                JSON.stringify({ url: o.upUrl, files: list }),
                json => {
                  const data = JSON.parse(json);
                  if (data.code === 200) {
                    this.$toast("修改成功！");
                    this.overlay = false;
                    this.getCompInfo();
                  } else {
                    this.$toast.fail("证件上传失败！");
                    this.overlay = false;
                  }
                }
              );
            } else {
              this.$toast("修改成功！");
              this.overlay = false;
              this.getCompInfo();
            }
          } else {
            this.overlay = false;
          }
        }
      });
    },
    //  更换图片
    imgChange(index) {
      // 选择图片
      this.imgShow = true;
      this.uploadIndex = index;
    },
    //  展示大图
    viewImg(uploadIndex) {
      this.imgShow = false;
      if (this.uploadList[uploadIndex].url) {
        this.showImg[0] = this.uploadList[uploadIndex].url;
        this.mask = true;
      }
    },
    //  选择资质图片
    reg_img_select(index) {
      this.imgShow = false;
      JNB._requestNative("openImageSelect", url => {
        if (url) {
          this.uploadList[index].url = url;
        }
      });
    },
    // 校验资质
    iQualification(list) {
      let flag = true;
      let message = "证件图片、营业执照信息必填";
      for (var i = 0; i < list.length; i++) {
        if (list[i].isShow && list[i].required && !list[i].url) {
          flag = false;
        }
        if (list[i].atype === 10) {
          if (
            list[i].certificateno === "" ||
            list[i].validitys === "" ||
            list[i].validitys === null ||
            list[i].validitye === "" ||
            list[i].validitye === null
          )
            flag = false;
        }
        // if(list[i].isShow && list[i].required && (list[i].atype === 13 || list[i].atype === 14)) {
        //   debugger
        //   flag = false;
        // }
        // if(list[i].isShow && list[i].required && list[i].atype === 13 && /^[\u4e00-\u9fa5]([A-D])[A-B][0-9].*$/.test(list[i].certificateno)) {
      }
      !flag && this.$toast.fail(message);
      if (!flag) {
        this.overlay = false;
      }
      return flag;
    },
    //  删除图片
    deleteImg(i) {
      this.imgList[0] = this.uploadList[i].url;
      this.imgList[0] = this.imgList[0].split("?")[0];
      Dialog.confirm({
        title: "温馨提示",
        message: "确认删除此证书吗？"
      })
        .then(() => {
          JNB._requestNative("delFiles", JSON.stringify(this.imgList), () => {
            this.uploadList[i].url = "";
          });
        })
        .catch(() => {});
    },
    placeholder(atype) {
      if (atype == 13 || atype == 14) {
        return "委托人";
      }
    },
    focusHeight() {
      this.$refs.button.style.marginBottom = 250 + "px";
      document.body.scrollTop = 9999;
      document.documentElement.scrollTop = 9999;
    },
    blurHeight() {
      this.$refs.button.style.marginBottom = 0;
    }
  }
};
</script>
<style scoped>
.modifyInfo {
  background-color: #fff;
  padding-top: 92px;
}
.modifyInfo .content {
  width: 710px;
  height: 300px;
